/*! Globales */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
}

a {
  text-decoration: none;
  font-family: "Roboto", sans-serif;
}

ul {
  list-style: none;
}

.centrar {
  text-align: center;
}
.centrar::after {
  margin: 0 auto;
}
.centrar::before {
  margin: 0 auto;
}

.capitalize {
  text-transform: capitalize;
}

.uppercasse {
  text-transform: uppercase;
}

.color {
  color: #fff;
}

.container {
  /*! Container - Movil */
  width: 95%;
  margin: 0 auto;
  /*! Container - Laptop */
  /*! Container - Desktop */
}
@media only screen and (min-width: 1200px) {
  .container {
    width: 90%;
    max-width: 120rem;
  }
}
@media only screen and (min-width: 1400px) {
  .container {
    width: 85%;
    max-width: 140rem;
  }
}

.no-margin {
  margin-bottom: 0 !important;
}

.header {
  background-image: linear-gradient(to top, rgba(29, 116, 145, 0.7529411765) 0%, rgba(71, 83, 129, 0.7529411765) 100%), url(/build/img/header.jpg);
  background-size: cover;
  background-position: top center;
}

.barra-header {
  padding: 2rem 0;
  background-color: rgba(6, 71, 97, 0.76);
  box-shadow: 0 0 5px 5px rgb(31, 96, 126);
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo .logotipo {
  font-size: 2.5rem;
  font-weight: bold;
  letter-spacing: 0.2rem;
}

.navegacion {
  display: none;
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .navegacion {
    display: flex;
  }
}

.enlace {
  display: inline-block;
  padding: 0.5rem 1.5rem;
}
.enlace::after {
  content: "";
  display: block;
  width: 10%;
  border: 0.2rem solid rgba(72, 255, 0, 0);
  transition: all 0.8s;
}
.enlace:hover::after {
  content: "";
  display: block;
  width: 100%;
  border: 0.2rem solid rgb(0, 255, 76);
}

.header-main {
  padding: 10rem 0 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header-main .user {
  text-align: center;
  padding: 1.5rem 0;
  /*! Table Vertical  Min:768px */
}
@media only screen and (min-width: 768px) {
  .header-main .user {
    font-size: 4.6rem;
  }
}
.header-main h3 {
  text-align: center;
  padding: 1.5rem 0;
  font-weight: 300;
  letter-spacing: 0.2rem;
}

.circulo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  animation-name: destello;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-delay: 1s;
}
.circulo img {
  position: absolute;
  width: 150px;
  height: auto;
  border-radius: 50%;
}

@keyframes destello {
  0% {
    box-shadow: 0px 0px 0px 1px rgba(6, 157, 184, 0.623);
  }
  50% {
    box-shadow: 0px 0px 5px 10px rgba(5, 161, 189, 0.5);
  }
  100% {
    box-shadow: 0px 0px 0px 10px rgba(255, 0, 0, 0);
  }
}
.redes-sociales,
.portafolio-enlaces {
  display: flex;
  gap: 2rem;
  margin-top: 4rem;
  justify-content: center;
}
.redes-sociales .redes,
.portafolio-enlaces .redes {
  display: inline-block;
}

.redes i {
  color: #333;
  font-size: 2.5rem;
  background-color: rgb(253, 197, 10);
  padding: 0.9rem 1.5rem;
  border-radius: 0.5rem;
}

/*! SOBRE MI */
.sobre-mi {
  padding: 5rem 0;
}

.content-sobre-mi {
  display: grid;
  gap: 3rem;
  padding: 3rem;
  border-radius: 0.2rem;
  box-shadow: 0 0 15px 2px rgba(51, 51, 51, 0.178);
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .content-sobre-mi {
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-sobre-mi {
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

h3 {
  font-size: 2.4rem;
  margin-bottom: 2rem;
}

p {
  margin-bottom: 1.5rem;
}

.sobre-mi span {
  font-weight: bold;
  display: block;
  margin-bottom: 1rem;
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .sobre-mi span {
    margin-right: 3rem;
    display: inline-block;
    width: 150px;
  }
}

/*! SKILLS */
.skills {
  padding: 5rem 0;
}

.skills h2 {
  text-align: center;
  margin-bottom: 4rem;
}

.content-skills {
  display: grid;
  padding: 3rem;
  border-radius: 0.2rem;
  box-shadow: 0 0 15px 2px rgba(51, 51, 51, 0.178);
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .content-skills {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.item {
  padding: 1.5rem 0;
}

.progres {
  height: 0.5rem;
  background-color: rgba(51, 51, 51, 0.445);
}

.nombre-item {
  display: flex;
  justify-content: space-between;
}

.siete {
  width: 70%;
  background-color: rgb(0, 176, 199);
  height: 100%;
}

.ocho {
  width: 80%;
  background-color: rgb(0, 176, 199);
  height: 100%;
}

.nueve {
  width: 90%;
  background-color: rgb(0, 176, 199);
  height: 100%;
}

/*! PORTAFOLIO */
.portafolio {
  padding: 5rem 0;
}
.portafolio h2 {
  text-align: center;
}

.portafolio-enlaces i {
  display: none;
  color: #333;
  font-size: 2.5rem;
  background-color: rgb(10, 176, 253);
  padding: 0.9rem 1.5rem;
  border-radius: 0.5rem;
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .portafolio-enlaces i {
    display: inline-block;
  }
}

.grid-portafolio {
  display: grid;
  padding: 8rem 0.2rem;
  gap: 3rem;
  overflow: hidden;
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .grid-portafolio {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 350px);
  }
}
.grid-portafolio .grid-item-portafolio {
  box-shadow: 0 0 5px rgb(139, 129, 129);
  opacity: 0.7;
  transition: all 0.5s;
}
.grid-portafolio .grid-item-portafolio:hover {
  cursor: pointer;
  opacity: 1;
}
.grid-portafolio img {
  width: 100%;
  height: 100%;
}

.power-bi {
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .power-bi {
    grid-column: 2/4;
    grid-row: 2/3;
  }
}

.soluciones {
  /*! Table Horizontal Min:992px */
}
@media only screen and (min-width: 992px) {
  .soluciones {
    grid-column: 1/2;
    grid-row: 1/3;
  }
}

/*! EXPERIENCE */
.experience {
  padding: 2rem 0;
}

.experience h2,
.education h2 {
  text-align: center;
  margin-bottom: 5rem;
}

.contenedor-experience {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.experience-item {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 5px rgb(139, 129, 129);
  /*! Table Vertical  Min:768px */
}
@media only screen and (min-width: 768px) {
  .experience-item {
    flex-direction: row;
  }
}

.titulo-experience {
  background-color: rgb(12, 92, 167);
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  /*! Table Vertical  Min:768px */
}
.titulo-experience p {
  margin: 0;
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .titulo-experience {
    width: 30%;
  }
}

.descripcion-experience {
  padding: 3rem;
  /*! Table Vertical  Min:768px */
}
@media only screen and (min-width: 768px) {
  .descripcion-experience {
    width: 65%;
  }
}

.experience-item {
  overflow: hidden;
}

/*! EDUCATION */
.education {
  padding: 7rem 0;
}

.titulo-education {
  background-color: rgb(12, 92, 167);
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  /*! Table Vertical  Min:768px */
}
.titulo-education p {
  margin: 0;
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .titulo-education {
    width: 30%;
  }
}

/*! CONTACT ME */
iframe {
  width: 100%;
}

.contacto {
  position: relative;
}
.contacto .container {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
.contacto h2 {
  text-align: center;
}

.formulario-contacto {
  background-color: rgb(255, 255, 255);
  padding: 4rem;
  width: 90%;
  margin: 0 auto;
  /*! Table Vertical  Min:768px */
}
@media only screen and (min-width: 768px) {
  .formulario-contacto {
    width: 60%;
  }
}

.formulario {
  display: grid;
  gap: 3rem;
  padding: 3rem 0;
  /*! Table Vertical  Min:768px */
}
@media only screen and (min-width: 768px) {
  .formulario {
    grid-template-columns: repeat(2, 1fr);
  }
}

label {
  display: inline-block;
  margin-bottom: 2rem;
}

input {
  display: block;
  width: 100%;
  outline: none;
  border: none;
  padding: 0 1rem;
}

.nombre {
  display: flex;
  align-items: center;
  border: 1px solid rgb(211, 211, 211);
  padding: 0.5rem 2rem;
  border-radius: 2rem;
  margin-bottom: 2rem;
}
.nombre span i {
  margin-top: 0.5rem;
  font-size: 2rem;
  color: rgb(122, 122, 122);
}

.mensaje {
  padding: 1rem;
}

textarea {
  border: none;
  outline: none;
  width: 100%;
  resize: none;
  border-bottom: 2px solid rgb(28, 93, 212);
}

textarea::placeholder {
  position: absolute;
  bottom: 1.5rem;
  text-align: right;
  touch-action: manipulation;
}

.boton {
  padding: 1rem;
}
.boton input {
  width: auto;
  padding: 1.5rem 3rem;
  border-radius: 0.5rem;
  background-color: rgb(28, 93, 212);
  color: #fff;
}
.boton input:hover {
  cursor: pointer;
}

.information h5 {
  margin-bottom: 2rem;
  font-size: 1.8rem;
  font-weight: normal;
}

/*! FOOTER */
.footer {
  background-color: rgb(12, 92, 167);
  padding: 2rem 0;
}
.footer p {
  margin: 0;
}

.footer-redes {
  display: flex;
  justify-content: center;
  gap: 7rem;
  /*! Table Vertical  Min:768px */
}
.footer-redes i {
  font-size: 3rem;
  color: rgb(248, 248, 248);
}
.footer-creador {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  /*! Table Vertical  Min:768px */
}
.footer-creador h4 {
  font-size: 2rem;
  color: #fff;
  letter-spacing: 2px;
}
.footer-creador p {
  font-size: 2rem;
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .footer-creador {
    flex-direction: row;
    justify-content: space-evenly;
  }
}